   width: 100%;
   text-align: center;
}



/*     Espacio Autor    */
.boxAutor{
    display: table-cell;
    width: 50%;
}

.pElementsUser{
    font-size: 8pt;
    color: #A6A6A6;
}
.positionRelative{
    margin-top: 40%;
}
.positionCenter{
    position: absolute;
	top: 50%;
	left: 50%;
	height: 30%;
	width: 50%;
	margin: -15% 0 0 -25%;
}
